.App {
  text-align: center;
  background-color: rgba(242, 233, 217, 0.5);
  height: 100vh; 
  display: flex;
  justify-content: center;
  align-items: center;
}

.content {
  height: 100vh;
  background-color: white;
  border-radius: 10px;
  overflow-y: auto;
  max-height: 90vh;
  width: 90%;
  opacity: 1;
}

.content::-webkit-scrollbar {
  width: 0;
}

.content::-webkit-scrollbar-thumb {
  background-color: transparent; /* Make the thumb transparent */
}

.content::-webkit-scrollbar-track {
  background-color: transparent; /* Make the track transparent */
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}


.navbar {
  height: 25vh;
  width: 8vh;
  position: fixed;
  z-index: 1;
  top: 50%;
  left: 2%;
  background-color: rgba(223, 206, 188, 0.5);
  overflow-x: hidden;
  padding-top: 20px;
  display: flex;
  flex-direction: column; 
  align-items: center;
  transform: translateY(-50%); /* Adjust to vertically center */
  border-radius: 10px;
  justify-content:  space-between; 
  transition: width 0.3s ease; 
  overflow: hidden;
}

.navbar a {
  display: flex;
  align-items: center; 
}

.section-img{
  margin: 5px;
  border-radius: 0%;
}

.navbar.expanded {
  width: 17vh; 
}

.navbar img {
  width: 30px;
  height: 30px;
}

.nav-item {
  display: flex;
  align-items: center;
  cursor: pointer;
}

.nav-text {
  display: none; /* Initially hide text */
  color: white;
  
}

.navbar.expanded .nav-text {
  display: inline; /* Show text when navbar is expanded */
}